img.cardImg {
  width: 100%;
  border-radius: 10px;
  display: inline-block;
}

.image {
  width: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.tourCard {
  padding: 10px;
  width: 100%;
  border-radius: 20px;
  border: 1px solid #ccc;
  position: relative;
}

.location {
  display: flex;
  align-items: center;
  color: #18b2dc;
  font-size: 14px;
  margin: 10px 15px;
}

.location p {
  font-family: "Inter", sans-serif;
}

h1.tripName {
  color: #0a1128;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 24px;
  margin: 0 15px;
}

p.tourDetailsShort {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #596157;
  margin: 10px 15px;
}

div.info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #596157;
  margin: 70px 15px 20px;
}
div.info p {
  font-family: "Inter", sans-serif;
}

.btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 15px;
}

.btns button {
  padding: 20px 35px;
  font-family: "Inter", sans-serif;
  color: #18b2dc;
  border: 2px solid #18b2dc;
  background-color: transparent;
  white-space: nowrap;
  font-weight: 600;
  cursor: pointer;
}

.btns button:hover {
  transition: background-color 0.5s;
  background-color: #18b2dc;
  color: #fff;
}

@media (max-width: 1024px) {
  h1.tripName {
    font-size: 1rem;
  }

  p.tourDetailsShort {
    font-size: 12px;
    line-height: 21px;
  }

  .btns {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    margin: 15px 0;
  }

  .btns button {
    padding: 10px 17px;
    font-family: "Inter", sans-serif;
    color: #18b2dc;
    border: 2px solid #18b2dc;
    background-color: transparent;
    font-size: 12px;
    white-space: nowrap;
    font-weight: 600;
    cursor: pointer;
    margin: 5px 0;
  }

  div.info {
    flex-direction: column;
    margin: 20px 15px 20px;
  }
}
